@import "../../global.scss";

.about-me {
  background-color: $blueColor;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  justify-content: space-evenly;

  h1 {
    font-family: "Gugi", cursive;
    font-size: 3rem;
    color: white;
    flex: 1;
    @include laptopLarge {
      overflow: hidden;
      font-size: 2rem;
    }

    @include tablet {
      font-size: 1.5rem;
    }
  }

  .content-container {
    flex: 8;
    display: flex;
    width: 90%;
    padding: 2rem;
    justify-content: space-around;
    align-items: center;

    @include laptopLarge {
      padding: 0;
      margin-top: 2rem;
      max-height: 90%;
    }

    @include tablet {
      flex-direction: column;
    }

    .img-container {
      width: 22rem;
      height: 22rem;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.4);

      @include laptopNormal {
        width: 15rem;
        height: 15rem;
      }

      @include tablet {
        width: 10rem;
        height: 10rem;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: bottom;
      }
    }

    .about-me {
      color: white;
      font-size: 1.2rem;
      line-height: 200%;
      width: 45%;
      padding: 0;
      overflow: auto;
      @include laptopLarge {
        flex: 1;
        margin-left: 3rem;
      }

      @include tablet {
        margin: 1rem auto;
        width: 95%;
      }
      span {
        font-family: "Cairo", sans-serif;
        margin-bottom: 1em;

        @include tablet {
          margin-bottom: 0.5em;
        }

        @include mobileLarge {
          font-size: 0.8rem;
          line-height: 200%;
        }
      }
    }
  }
}
