$mainColor: #fc801b;

$blueColor: #0096b0;

$greyColor: #a6a5a6;

$boxShadow: 0px 3px 10px rgba(0, 0, 0, 0.4);

$laptopWidth: 1440px;

@mixin laptopLarge {
  @media (max-width:#{$laptopWidth}) {
    @content;
  }
}

$laptopNormalWidth: 1024px;

@mixin laptopNormal {
  @media (max-width:#{$laptopNormalWidth}) {
    @content;
  }
}

$tabletWidth: 768px;

@mixin tablet {
  @media (max-width:#{$tabletWidth}) {
    @content;
  }
}

$mobileLargeWidth: 425px;

@mixin mobileLarge {
  @media (max-width:#{$mobileLargeWidth}) {
    @content;
  }
}

$mobileWidth: 375px;

@mixin mobile {
  @media (max-width:#{$mobileWidth}) {
    @content;
  }
}

$mobileSmallWidth: 320px;

@mixin mobileSmall {
  @media (max-width:#{$mobileSmallWidth}) {
    @content;
  }
}

* {
  font-family: "Cairo", sans-serif;
  font-family: "Gugi", cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}
