@import "../../global.scss";

.portfolio {
  background-color: lightgray;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  h1 {
    font-family: "Gugi", cursive;
    font-size: 3rem;
    color: white;

    @include laptopLarge {
      overflow: hidden;
      font-size: 2rem;
    }

    @include tablet {
      font-size: 1.5rem;
    }
  }

  ul {
    margin: 1rem;
    padding: 0;
    list-style: none;
    display: flex;
    width: 90%;
    justify-content: space-around;

    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }
    @include mobile {
      width: 100%;
      justify-content: flex-start;
      padding: 0 2em;
    }

    li {
      font-family: "Gugi", cursive;
      font-size: 1.5rem;
      color: white;
      padding: 0.7rem;
      border-radius: 10px;
      cursor: pointer;
      @include laptopLarge {
        overflow: hidden;
        font-size: 1.2rem;
      }
      @include tablet {
        font-size: 1rem;
        padding: 0.5rem;
        margin-right: 2em;
      }

      @include mobileLarge {
        font-size: 0.8rem;
        margin-right: 1em;
      }

      &.active {
        text-decoration: underline;
        text-underline-offset: 0.3rem;
      }
    }
  }

  .container {
    width: 90%;
    height: 80%;
    margin-top: 0.5rem;
    overflow-y: auto;

    @include mobile {
      height: 90%;
      width: 100%;
    }
    ::-webkit-scrollbar {
      width: 0.5em;
    }
    ::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }
    ::-webkit-scrollbar-thumb {
      background-color: darkgrey;
    }
  }
}
