@import "../../global.scss";

.works {
  background-color: $mainColor;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 2rem;
  flex-direction: column;
  overflow: hidden;
  .top-section {
    flex: 1 0 100%;
    width: 100%;

    h1 {
      text-align: center;
      font-family: "Gugi", cursive;
      font-size: 3rem;
      color: white;
      @include laptopLarge {
        overflow: hidden;
        font-size: 2rem;
      }

      @include tablet {
        font-size: 1.5rem;
      }
      @include mobileLarge {
        font-size: 1.3rem;
      }
    }

    ul {
      margin: 1rem;
      padding: 0;
      list-style: none;
      display: flex;
      width: 90%;
      align-items: center;
      justify-content: center;
      justify-content: space-around;
      @include tablet {
        flex-wrap: wrap;
        justify-content: center;
      }
      @include mobileLarge {
        width: 100%;
        justify-content: flex-start;
        padding: 0 2em;
      }

      li {
        font-family: "Gugi", cursive;
        font-size: 1.5rem;
        color: white;
        padding: 0.7rem;
        border-radius: 10px;
        cursor: pointer;
        @include laptopLarge {
          overflow: hidden;
          font-size: 1.2rem;
        }
        @include tablet {
          font-size: 1rem;
          padding: 0.5rem;
          margin-right: 2em;
        }

        @include mobileLarge {
          font-size: 0.8rem;
        }

        &.active {
          text-decoration: underline;
          text-underline-offset: 0.3rem;
        }
      }
    }
  }

  .arrow {
    height: 3rem;
    position: absolute;
    cursor: pointer;

    @include mobileLarge {
      display: none;
    }

    &.left {
      left: 5em;
      transform: rotateY(180deg);

      @include laptopNormal {
        left: 2em;
      }
    }

    &.right {
      right: 5em;
      @include laptopNormal {
        right: 2em;
      }
    }
  }

  .slider {
    height: 30em;
    display: flex;
    position: absolute;
    margin-top: 5em;
    left: 0;
    transition: all 1s ease-out;

    @include laptopLarge {
      height: 26rem;
    }

    @include mobileLarge {
      height: 80%;
      justify-content: space-around;
      top: 15%;
      flex-wrap: wrap;
      width: 100vw;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      @include laptopLarge {
        align-items: flex-start;
      }

      @include mobileLarge {
        flex-direction: column;
        flex-wrap: wrap;
      }

      .item {
        width: 70vw;
        height: 90%;
        margin-top: 2em;
        background-color: white;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);

        @include mobileLarge {
          margin: 0.5rem auto;
          height: 20em;
          width: 80vw;
        }

        @include mobileSmall {
          height: 25em;
        }
      }
      .left {
        flex: 60%;
        height: 95%;
        padding: 1em 2em;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .imgContainer {
          flex: 2;
          width: 100%;
          margin: 0.5em auto;
          display: flex;
          align-items: center;
          justify-content: space-evenly;

          @include mobileLarge {
            flex: 1;
          }

          img {
            width: 2em;
            margin: 0 auto;

            @include laptopLarge {
              margin: 0 1rem;
            }
            @include tablet {
              margin: 0;
            }

            @include mobileLarge {
              width: 0.8em;
            }
          }
        }

        .front-end-imgContainer {
          img {
            width: 3em;
            @include laptopLarge {
              margin: 0 1rem;
            }
            @include tablet {
              margin: 0;
            }

            @include mobileLarge {
              width: 1.5em;
            }
          }
        }

        h2 {
          flex: 2;
          text-align: center;
          font-family: "Cairo", sans-serif;
          font-size: 2rem;
          @include laptopLarge {
            font-size: 1.5rem;
          }

          @include laptopNormal {
            font-size: 1.3rem;
          }

          @include mobileLarge {
            font-size: 1rem;
          }
        }
        p {
          flex: 5;
          font-family: "Cairo", sans-serif;
          font-weight: 300;
          font-size: 1rem;

          @include mobileLarge {
            font-size: 0.8rem;
            margin: 0.5rem auto;
          }
        }

        .links {
          flex: 1;
          display: flex;
          align-items: center;

          @include mobileLarge {
            justify-content: space-between;
          }
        }

        span {
          font-family: "Cairo", sans-serif;
          font-size: 1rem;
          font-weight: 700;
          cursor: pointer;

          @include laptopNormal {
            font-size: 0.8rem;
          }

          @include mobileLarge {
            font-size: 0.8rem;
            text-align: center;
          }
          a {
            text-decoration: none;
            color: inherit;
          }
        }
        span:hover {
          text-decoration: underline;
        }
      }
      .right {
        flex: 60%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        @include tablet {
          display: none;
        }

        img {
          width: 95%;
          height: 95%;
          object-fit: contain;
        }
      }
    }
  }
}
